.project-goal {
  padding-top: 30px;
  padding-bottom: 120px;

  .grid {
    @include minitablet {
      display: block;
    }
  }

  &__left {
    grid-column: 6 span;
    padding-top: 25px;
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 6 span;

    @include minitablet {
      justify-content: flex-start;
    }
  }

  &__title {
    margin-bottom: 40px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 25px;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-gray);
  }

  .project-info {
    border-radius: 4px;
    padding: 25px 60px;
    padding-right: 40px;
    width: 495px;
    min-height: 300px;
    box-shadow: var(--main-shadow);
    background-color: var(--color-light);

    &__item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    &__category {
      flex-shrink: 0;
      width: 130px;
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      text-transform: uppercase;
      color: var(--color-ebony);
    }

    &__value {
      font-size: 16px;
      line-height: 160%;
      color: var(--color-storm);
    }

    @include minitablet {
      padding: 15px 20px;
      width: 100%;
    }
  }

  @include minitablet {
    padding: 40px 0;
  }
}
